          <span class="hljs-attr">defaultResetView</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">105</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">29.999999999999993</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">19059568.497290563</span> },
          <span class="hljs-comment">// 用于启用或禁用罗盘。true是启用罗盘，false是禁用罗盘。默认值为true。如果将选项设置为false，则罗盘将不会添加到地图中。</span>
          <span class="hljs-attr">enableCompass</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-comment">// 用于启用或禁用缩放控件。true是启用，false是禁用。默认值为true。如果将选项设置为false，则缩放控件将不会添加到地图中。</span>
