          <span class="hljs-attr">enableDistanceLegend</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-comment">// 用于启用或禁用指南针外环。true是启用，false是禁用。默认值为true。如果将选项设置为false，则该环将可见但无效。</span>
          <span class="hljs-attr">enableCompassOuterRing</span>: <span class="hljs-literal">true</span>
        }
