        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>defaultResetView</td>
<td>Object</td>
<td>中国</td>
<td><code v-pre>optional</code> 指定重置按钮的位置。<strong>结构：{lng: number, lat: number, height: number} or {west: number,south: number,east: number,north: number}</strong></td>
</tr>
<tr>
<td>enableCompass</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> 指定是否启用罗盘部件。</td>
</tr>
<tr>
<td>enableZoomControls</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> 指定是否启用缩放部件。</td>
</tr>
<tr>
<td>enableDistanceLegend</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> 指定是否启用距离比例尺部件。</td>
</tr>
<tr>
<td>enableCompassOuterRing</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> 指定罗盘部件是否可用。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data () {
        return {
          options: {
            // 用于在使用重置导航重置地图视图时设置默认视图控制。接受的值是经纬度{lng: number, lat: number, height: number}或者 rectangle{west: number,south: number,east: number,north: number}
            defaultResetView: { lng: 105, lat: 29.999999999999993, height: 19059568.497290563 },
            // 用于启用或禁用罗盘。true是启用罗盘，false是禁用罗盘。默认值为true。如果将选项设置为false，则罗盘将不会添加到地图中。
            enableCompass: true,
            // 用于启用或禁用缩放控件。true是启用，false是禁用。默认值为true。如果将选项设置为false，则缩放控件将不会添加到地图中。
            enableZoomControls: true,
            // 用于启用或禁用距离图例。true是启用，false是禁用。默认值为true。如果将选项设置为false，距离图例将不会添加到地图中。
            enableDistanceLegend: true,
            // 用于启用或禁用指南针外环。true是启用，false是禁用。默认值为true。如果将选项设置为false，则该环将可见但无效。
            enableCompassOuterRing: true
          }
        }
      },
      methods: {
        ready (cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const {Cesium, viewer} = this.cesiumInstance
        }
      }
    }
  </script>